 <pre class="ne-codeblock language-cpp" data-language="cpp" id="AGB1Y"><span style="font-weight:bold;">2.3</span> printf<code>#include &lt;cstdio&gt;					//Cscanfprintf
using namespace std;
int main()
{
	int a=3,b=4,c;
	double d=3.1415926,e=3.1415927,f;
    printf("a=%-4d|b=%4d|c=%d\n",a,b,c);			//,-
    printf("d=%.2f|e=%-8.2f|f=%8f\n",d,e,f);		//,-
    printf("character %c\n",'A');					//
}</code></pre>
