using namespace std;
int main()
{
    int seconds;
    cin&gt;&gt;seconds;
    int days = 60*60*24;							//
    int months = 30;
    int years = months*12;
    int day = seconds/days;							//
    int year = 2011+day/years;
    day = day-day/years*years;						//
    int month = day/ months;
    day = day-month* months;
    cout&lt;&lt;year&lt;&lt;' '&lt;&lt;month+1&lt;&lt;' '&lt;&lt;day+1&lt;&lt;endl;		//1
    return 0;
}</code></pre>
